function Hero() {
  return (
    <section id="hero" class="hero d-flex align-items-center">
      <div class="container">
        <div class="row">
          <div class="col-lg-6 d-flex flex-column justify-content-center">
            <h1 data-aos="fade-up">
              Assignment submission made easy using EasyAssignmentSubmit
            </h1>
            <h2 data-aos="fade-up" data-aos-delay="400">
              Hey Welcome!!! You came to the right place,login and submit your
              assignments online within minutes
            </h2>
            <div data-aos="fade-up" data-aos-delay="600">
              <div class="text-center text-lg-start">
                <a
                  href="#login"
                  class="
                    btn-get-started
                    scrollto
                    d-inline-flex
                    align-items-center
                    justify-content-center
                    align-self-center
                    remove-underline
                  "
                >
                  <span>Get Started</span>
                  <i class="bi bi-arrow-right"></i>
                </a>
              </div>
            </div>
          </div>
          <div
            class="col-lg-6 hero-img"
            data-aos="zoom-out"
            data-aos-delay="200"
          >
            <img
              src={require("../assets/images/hero-img.png").default}
              class="img-fluid"
              alt=""
            />
          </div>
        </div>
      </div>
    </section>
  );
}

export default Hero;
